          </div>
          <div style="display: flex; flex: 1; align-items: center; margin-top: 15px">
            <div style="width: 100px; text-align: left">
              <label>S ca:</label>
            </div>
            <div>
              <el-input-number size="medium" :min="2" v-model="post.numberDoor"></el-input-number>
            </div>
          </div>
          <div style="display: flex; flex: 1; align-items: center; margin-top: 15px">
            <div style="width: 100px; text-align: left">
