            </div>
            <div>
              <el-input-number size="medium" :min="2" v-model="post.numberSeat"></el-input-number>
            </div>
          </div>
        </el-card>
      </div>
      <div style="flex: 1">
        <el-card>
          <div style="text-align: left; margin-top: -15px">
            <h3 style="font-size: 15px">Hp s truyn ng</h3>
          </div>
          <div style="display: flex; flex: 1; align-items: center; margin-top: 10px">
            <div style="width: 160px ;text-align: left">
              <label>Hp s:</label>
            </div>
            <div>
              <el-select placeholder="Hp s" v-model="post.gearType">
                <el-option value="AT" label="S tay"></el-option>
                <el-option value="MT" label="S t ng"></el-option>
              </el-select>
            </div>
          </div>
          <div style="display: flex; flex: 1; align-items: center; margin-top: 10px">
            <div style="width: 160px ;text-align: left">
              <label>Dn ng:</label>
            </div>
            <div>
              <el-select placeholder="Hp s" v-model="post.wheelType">
                <el-option value="FWD" label="FWD - Dn ng cu trc"></el-option>
                <el-option value="RWD" label="RWD - Dn ng cu sau"></el-option>
                <el-option value="4WD" label="4WD - Dn ng 4 bnh"></el-option>
                <el-option value="AWD" label="AWD - Dn ng 4 bnh ton thi gian"></el-option>
              </el-select>
            </div>
          </div>
        </el-card>
        <el-card>
          <div style="text-align: left; margin-top: -15px">
            <h3 style="font-size: 15px">Nhin liu</h3>
          </div>
          <div style="display: flex; flex: 1; align-items: center; margin-top: 10px">
            <div style="width: 160px ;text-align: left">
              <label>Nhin liu:</label>
            </div>
            <div>
              <el-select placeholder="Nhin liu" v-model="post.fuelType">
                <el-option value="GASOLINE" label="Xng"></el-option>
                <el-option value="DIESEL" label="Diesel"></el-option>
                <el-option value="HYBRID" label="Hibrid"></el-option>
                <el-option value="ELECTRIC" label="in"></el-option>
              </el-select>
            </div>
          </div>
          <div style="display: flex; flex: 1; align-items: center; margin-top: 10px">
            <div style="width: 160px ;text-align: left">
              <label>Mc tiu th nhin liu:</label>
            </div>
            <div>
              <el-input-number v-model="post.fuelConsumption"></el-input-number> L/100Km
            </div>
          </div>
        </el-card>
        <el-card>
          <div style="text-align: left; margin-top: -15px">
            <h3 style="font-size: 15px">Thm m t</h3>
          </div>
          <div>
            <el-input type="textarea" :rows="5" v-model="post.description" style="word-break: normal"></el-input>
          </div>
        </el-card>
      </div>
    </div>
    <div>
      <el-card>
        <div style="text-align: left; margin-top: -15px">
          <h3 style="font-size: 15px">Thng tin lin h</h3>
        </div>
        <div>
          <div style="display: flex">
            <div style="display: flex; align-items: center">
              <label style="width: 60px; text-align: left" >Tn:</label>
              <el-input v-model="post.contact.fullName"></el-input>
            </div>
            <div style="display: flex; align-items: center; margin-left: 10px">
              <label style="width: 110px" >in thoi:</label>
              <el-input v-model="post.contact.phoneNumber"></el-input>
            </div>
            <div style="display: flex; align-items: center; margin-left: 10px">
              <label style="width: 150px">in thoi khc:</label>
              <el-input v-model="post.contact.anotherPhoneNumber"></el-input>
            </div>
          </div>
          <div style="display: flex; align-items: center;margin-top: 10px">
            <div style="display: flex; align-items: center">
              <label style="width: 60px; text-align: left" >a ch:</label>
              <el-input style="width: 400px" v-model="post.contact.address" size="mini"></el-input>
            </div>
            <div style="margin-left: 20px">
              <label style="width: 60px;; align-items: center" >Tnh:</label>
              <el-select v-model="post.contact.province" size="mini" placeholder="Chn tnh/thnh ph" clearable filterable>
                <el-option
                        v-for="(province, index) in provinces"
                        :key="index"
                        :label="province"
                        :value="province">
                </el-option>
              </el-select>
            </div>
          </div>
        </div>
        <div style="float: left">
          <el-button type="text">Thay i thng tin lin h</el-button>
        </div>
      </el-card>
      <el-card>
        <div style="text-align: left; margin-top: -15px">
          <h3 style="font-size: 15px">ng nh cho xe</h3>
          <!--          <img src="http://localhost:9090/images/gtr.jpg">-->
          <div v-if="imageList.length > 0" style="display: flex">
            <div v-for="(image, index) in imageList"
                 :key="index">
              <div style="display: flex">
                <el-image style="width: 150px; height: 150px; margin: 8px;border: orangered"
                          :src="image.url"
                          fit="cover"></el-image>
                <span
                        class="close-button-small"
                        style="cursor: pointer"
                        @click="removeImage(image)"
                >X</span>
              </div>
            </div>
          </div>
        </div>
        <!--        <div>-->
        <!--          <el-button style="margin-top: 16px; margin-bottom: 32px;margin-left: 8px" size="small" type="primary" @click="submitUpload">-->
        <!--            Upload to server-->
        <!--          </el-button>-->
        <!--          <el-upload ref="upload" class="upload-demo" drag-->
        <!--                     :action="'http://localhost:9090/api/image/upload'"-->
        <!--                     :auto-upload="false" :on-success="uploaded"-->
        <!--                     :on-error="error"-->
        <!--                     :file-list="fileList"-->
        <!--                     multiple-->
        <!--                     list-type="picture">-->
        <!--            <i class="el-icon-upload"></i>-->
        <!--            <div class="el-upload__text">-->
        <!--              Drop file here or-->
        <!--              <em>click to upload</em>-->
        <!--            </div>-->
        <!--          </el-upload>-->
        <!--        </div>-->
        <el-button @click="openListImage">Thm hnh nh</el-button>
      </el-card>
    </div>
    <div style="margin-top: 15px">
      <el-button type="success" style="margin-left: 45%" @click="savePost">Lu</el-button>
    </div>
  </div>
</template>

<script>
import ImageList from "@/views/components/ImageList";
import CheckEmpty from "@/lib/CheckEmpty";
import {api} from "@/lib/api";
import {app} from "@/lib/app";
import stores from "@/lib/stores";

export default {
    props:{
        selectedPost: Object,
        reason:String
    },
    components:{
        ImageList,
    },
    data(){
        return{
            post:{
                brand:'',
                name:'',
                originType: '',
                carType:'',
                numberOfKmMoved: 0,
                yearOfManufacture:null,
                wheelType:'',
                version:'',
                vehicleType:'',
                price:0,
                numberSeat:0,
                numberDoor:0,
                gearType:'',
                fuelType:'',
                fuelConsumption:0,
                description:'',
                colorOutside:'',
                colorInside:'',
                capacity:0,
                contact:{
                    fullName:'',
                    phoneNumber: '',
                    anotherPhoneNumber:'',
                    address:''
                }
            },
            fileList:[],
            visibleImage: false,
            imageList:[],
            provinces:[],
            reasonPost:''
        }
    },
    methods:{
        selectedImages(payload){
            this.imageList = this.imageList.concat(payload);
            this.visibleImage = false;
            console.log(this.imageList);
        },
        removeImage(image){
            console.log(image);
            this.imageList.splice(this.imageList.indexOf(image),1);
        },
        openListImage(){
            this.visibleImage = true;
        },
        async findPostByUserId(){
            var response = await api.callAPI(
                "GET",
                "/api/post/findByUserId?userId="
                + app.user.id
                +"&page=" + 1
                +"&pageSize=" + 20,
                null
            );
            if (response.data){
                this.posts = response.data.content;
            }
        },
        async savePost(){
            if (CheckEmpty.checkInput(this.post.name))
                return this.alertError("Bn cha nhp tn xe");
            if (CheckEmpty.checkInput(this.post.version))
                return this.alertError("Bn cha nhp phin bn");
            if (CheckEmpty.checkInput(this.post.price))
                return this.alertError("Bn cha nhp gi");
            if (CheckEmpty.checkInput(this.post.version))
                return this.alertError("Bn cha nhp phin bn");
            if (this.post.yearOfManufacture == null)
                return this.alertError("Bn cha chn nm sn xut");
            if (CheckEmpty.checkInput(this.post.colorInside))
                return this.alertError("Bn cha nhp mu ni tht");
            if (CheckEmpty.checkInput(this.post.colorOutside))
                return this.alertError("Bn cha mu ngoi tht");
            if (CheckEmpty.checkInput(this.post.description))
                return this.alertError("Bn cha nhp m t cho xe");
            if (CheckEmpty.checkInput(this.post.contact.fullName))
                return this.alertError("Bn cha nhp thng tin lin h");
            if (!this.validPhoneNumber(this.post.contact.phoneNumber))
                    return this.alertError("Sai nh dng s in thoi");
            if (!this.validPhoneNumber(this.post.contact.anotherPhoneNumber))
                    return this.alertError("Sai nh dng s in thoi khc");

            if (this.imageList.length == 0){
                return this.alertError("Bn cha chn hnh nh");
            }
            if (this.post.type == "NEW") this.post.numberOfKmMoved = 0;
            this.post.images = this.imageList;
            this.post.userId = app.user.id;
            var response = await api.callAPI("POST","/api/post/update",this.post);
            if (response.data){
                this.alertSuccess("Lu thnh cng");
                this.findPostByUserId();
            }
        },
        validPhoneNumber(phoneNumber){
            return /((09|03|07|08|05)+([0-9]{8})\b)/g.test(phoneNumber);
        },
    },
    beforeMount(){
        this.post = this.selectedPost;
        this.post.yearOfManufacture = null;
        this.imageList = this.post.images;
        this.provinces = stores.provinces;
        this.reasonPost = this.reason;
        console.log(this.reasonPost);
    }
    }
</script>

