          </div>
          <div>
            <el-select placeholder="Hp s" v-model="post.gearType">
              <el-option value="MT" label="S tay"></el-option>
              <el-option value="AT" label="S t ng"></el-option>
            </el-select>
          </div>
        </div>
        <div style="display: flex; flex: 1; align-items: center; margin-top: 10px">
          <div style="width: 160px ;text-align: left">
            <label>Dn ng:</label>
          </div>
          <div>
            <el-select placeholder="Hp s" v-model="post.wheelType">
              <el-option value="FWD" label="FWD - Dn ng cu trc"></el-option>
              <el-option value="RWD" label="RWD - Dn ng cu sau"></el-option>
              <el-option value="4WD" label="4WD - Dn ng 4 bnh"></el-option>
              <el-option value="AWD" label="AWD - Dn ng 4 bnh ton thi gian"></el-option>
            </el-select>
          </div>
        </div>
      </el-card>
      <el-card>
        <div style="text-align: left; margin-top: -15px">
          <h3 style="font-size: 15px">Nhin liu</h3>
        </div>
        <div style="display: flex; flex: 1; align-items: center; margin-top: 10px">
          <div style="width: 160px ;text-align: left">
            <label>Nhin liu:</label>
          </div>
          <div>
            <el-select placeholder="Nhin liu" v-model="post.fuelType">
              <el-option value="GASOLINE" label="Xng"></el-option>
              <el-option value="DIESEL" label="Diesel"></el-option>
              <el-option value="HYBRID" label="Hibrid"></el-option>
              <el-option value="ELECTRIC" label="in"></el-option>
            </el-select>
          </div>
        </div>
        <div style="display: flex; flex: 1; align-items: center; margin-top: 10px">
          <div style="width: 160px ;text-align: left">
            <label>Mc tiu th nhin liu:</label>
          </div>
          <div>
            <el-input-number v-model="post.fuelConsumption"></el-input-number> L/100Km
          </div>
        </div>
      </el-card>
      <el-card>
        <div style="text-align: left; margin-top: -15px">
          <h3 style="font-size: 15px">Thm m t</h3>
        </div>
        <div>
          <el-input type="textarea" :rows="5" v-model="post.description" style="word-break: normal"></el-input>
        </div>
      </el-card>
    </div>
    </div>
    <div>
      <el-card>
        <div style="text-align: left; margin-top: -15px">
          <h3 style="font-size: 15px">Thng tin lin h</h3>
        </div>
        <div>
          <div style="display: flex">
            <div style="display: flex; align-items: center">
            <label style="width: 60px; text-align: left" >Tn:</label>
            <el-input v-model="post.contact.fullName" size="mini"></el-input>
            </div>
            <div style="display: flex; align-items: center; margin-left: 10px">
              <label style="width: 110px" >in thoi:</label>
              <el-input v-model="post.contact.phoneNumber" size="mini"></el-input>
            </div>
            <div style="display: flex; align-items: center; margin-left: 10px">
              <label style="width: 150px">in thoi khc:</label>
              <el-input v-model="post.contact.anotherPhoneNumber" size="mini"></el-input>
            </div>
          </div>
          <div style="display: flex; align-items: center;margin-top: 10px">
            <div style="display: flex; align-items: center">
            <label style="width: 60px; text-align: left" >a ch:</label>
            <el-input v-model="post.contact.address" size="mini" style="width: 400px"></el-input>
            </div>
            <div style="margin-left: 20px">
              <label style="width: 60px;; align-items: center" >Tnh:</label>
              <el-select v-model="post.contact.province" size="mini" placeholder="Chn tnh/thnh ph" clearable filterable>
                <el-option
                v-for="(province, index) in provinces"
                :key="index"
                :label="province"
                :value="province">
                </el-option>
              </el-select>
