<!--          <img src="http://localhost:9090/images/gtr.jpg">-->
          <div v-if="images.length > 0" style="display: flex">
          <div v-for="(image, index) in images"
            :key="index">
            <div style="display: flex">
            <el-image style="width: 150px; height: 150px; margin: 8px;border: orangered"
                      :src="image.url"
                      fit="cover"></el-image>
            <span
                    class="close-button-small"
                    style="cursor: pointer"
                    @click="removeImage(image)"
            >X</span>
            </div>
          </div>
          </div>
        </div>
<!--        <div>-->
<!--          <el-button style="margin-top: 16px; margin-bottom: 32px;margin-left: 8px" size="small" type="primary" @click="submitUpload">-->
<!--            Upload to server-->
<!--          </el-button>-->
<!--          <el-upload ref="upload" class="upload-demo" drag-->
<!--                     :action="'http://localhost:9090/api/image/upload'"-->
<!--                     :auto-upload="false" :on-success="uploaded"-->
<!--                     :on-error="error"-->
<!--                     :file-list="fileList"-->
<!--                     multiple-->
<!--                     list-type="picture">-->
<!--            <i class="el-icon-upload"></i>-->
<!--            <div class="el-upload__text">-->
<!--              Drop file here or-->
<!--              <em>click to upload</em>-->
<!--            </div>-->
<!--          </el-upload>-->
<!--        </div>-->
        <el-button @click="openListImage">Thm hnh nh</el-button>
      </el-card>
    </div>
    <div style="margin-top: 15px">
      <el-button type="success" @click="UpPost">ng tin</el-button>
    </div>
  </div>
</template>

<script>
    import {api} from "@/lib/api";
    import CheckEmpty from "@/lib/CheckEmpty"
    import ImageList from "@/views/components/ImageList";
    import {app} from "@/lib/app";
    import stores from "@/lib/stores";

    export default {
        // props: {
        //     selectedPost: Object
        // },
        components:{
          ImageList
        },
        data(){
            return{
                searchName:"",
                post:{
                  brand:'',
                  name: "",
                  originType: '',
                  carType:'',
                  numberOfKmMoved: 0,
                  yearOfManufacture:null,
                  wheelType:'',
                    version:'',
                    vehicleType:'',
                    price:0,
                    numberSeat:0,
                    numberDoor:0,
                    gearType:'',
                    fuelType:'',
                    fuelConsumption:0,
                    description:'',
                    colorOutside:'',
                    colorInside:'',
                    capacity:0,
                    contact:{
                        fullName:'',
                        phoneNumber: '',
                        anotherPhoneNumber:'',
                        address:'',
                        province:''
                    }
                },
                fileList:[],
                visibleImage: false,
                images:[],
                provinces:[]
            }
        },
        methods:{
            async UpPost(){
                if (CheckEmpty.checkInput(this.post.name))
                    return this.alertError("Bn cha nhp tn xe");
                if (CheckEmpty.checkInput(this.post.version))
                    return this.alertError("Bn cha nhp phin bn");
                if (CheckEmpty.checkInput(this.post.price))
                    return this.alertError("Bn cha nhp gi");
                if (CheckEmpty.checkInput(this.post.colorInside))
                    return this.alertError("Bn cha nhp mu ni tht");
                if (CheckEmpty.checkInput(this.post.colorOutside))
                    return this.alertError("Bn cha mu ngoi tht");
                if (CheckEmpty.checkInput(this.post.description))
                    return this.alertError("Bn cha nhp m t cho xe");
                if (CheckEmpty.checkInput(this.post.contact.fullName))
                    return this.alertError("Bn cha nhp thng tin lin h");
                if (!this.validPhoneNumber(this.post.contact.phoneNumber))
                    return this.alertError("Sai nh dng s in thoi");
                if (!this.validPhoneNumber(this.post.contact.anotherPhoneNumber))
                    return this.alertError("Sai nh dng s in thoi khc");
                if (CheckEmpty.checkInput(this.post.contact.address))
                    return this.alertError("Bn cha nhp a ch")
                if (CheckEmpty.checkInput(this.post.contact.province))
                    return this.alertError("Bn cha chn thnh ph")
                if (this.images.length == 0)
                    return this.alertError("Bn cha chn hnh nh");
                this.post.images = this.images;
                this.post.userId = app.user.id;
                var response = await api.callAPI("POST","/api/post/create",this.post);
                if (response.data){
                    this.alertSuccess("Lu thnh cng. Bi vit  c gi ti Admin  xt duyt")
                }
            },
            handlePreview(){

            },
            openListImage(){
              this.visibleImage = true;
            },
            selectedImages(payload){
                this.images = payload;
                this.visibleImage = false;
            },
            removeImage(image){
                this.images.splice(this.images.indexOf(image),1);
            },
            validPhoneNumber(phoneNumber){
                return /((09|03|07|08|05)+([0-9]{8})\b)/g.test(phoneNumber);
            },
        },
        beforeMount(){
            this.provinces = stores.provinces;
        }
    }
</script>

